import React from 'react';
import CKEditor from '@ckeditor/ckeditor5-react';
import BallonBlockEditor from '@ckeditor/ckeditor5-build-balloon-block';

class CKEditorWidget extends React.Component {
  controlID = `rich-editor-${Math.random()}`;

  render() {
    const {
      forID,
      classNameWrapper,
      setActiveStyle,
      setInactiveStyle,
      value,
      onChange,
    } = this.props;
    return (
      <div
        className={classNameWrapper}
        style={{
          paddingLeft: 10,
          paddingRight: 10,
          minHeight: 200,
        }}
      >
        <CKEditor
          id={forID}
          editor={BallonBlockEditor}
          onInit={(editor) => {
            this.editor = editor;
            editor.config.set('insertImage.openMediaLibrary', () =>
              this.openMediaLibrary(),
            );
          }}
          data={value}
          onChange={(event, editor) => {
            const data = editor.getData();
            onChange(data);
          }}
          onBlur={() => setInactiveStyle()}
          onFocus={() => setActiveStyle()}
          style={{ outline: 'none' }}
        />
      </div>
    );
  }
}

export default CKEditorWidget;

